import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import goods from '@/common/model/goods/index';
export default {
	components: {
		uniLoadMore,
		goods,
	},
	data() {
		return {
			items: []
		};
	},
	
	onLoad(options){



		this.loadData();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			if(type === 'refresh'){
				this.items = [];
			}
			
			this.$http.post('/cms/goods/list', {ipage:0}).then(res => {
				if(res.data.items){
					this.items.push(
					...res.data.items
					);
				}
				//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
				this.loadingType  = res.data.items.length > 20 ? 'nomore' : 'more';
				if(type === 'refresh'){
					if(loading == 1){
						uni.hideLoading()
					}else{
						uni.stopPullDownRefresh();
					}
				}
			}).catch(err => {});
		},
		checkin(){
			this.$http.post('/cms/member/member/check_in', {}).then(res => {
				this.$api.msg(res.info.status_err);
			}).catch(err => {});
		},
		stopPrevent(){}
	},
}
